.container{
	width: 860px;
	height: calc(100vh - 120px);
	background-color: rgba(0, 0, 0, 0.6);
	margin: 0 auto;
	padding: 20px;
	position: absolute;
	left:50%;
	margin-left: -430px;
	margin-top: 110px;
	z-index: 1;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.friendsList{
	width: 28%;
}
.friendsList p{
	font-family: Intro;
	font-weight: 400;
	font-size: 15px;	
	color: white;
	/* margin: 20px; */
	text-transform: uppercase;
	padding: 15px 10px;
	border-bottom: 2px solid rgb(255, 200, 0);
	
}
.MessagesList{
	width: 70%;
	background-color: rgba(0, 0, 0, 0.6);
	padding: 20px;
}
.MessagesField{
	border: 1px solid rgb(255, 200, 0);
    border-radius: 10px;
    height: 87%;
    padding: 20px;	
}
.friendItem{
	padding: 10px;
	display: block;	
	text-decoration: none;
	
}
.active{
	background-color: rgba(255, 200, 0, 0.4);	
}
.active i{
	color: rgb(0, 0, 0);	
}
.friendItem:hover{
	background-color: rgba(255, 200, 0, 0.4);	
}
.friendItem:hover span{
	color: rgb(255, 255, 255);
}
.friendItem div{
	width: 60px;
	height: 60px;
	border-radius: 50%;
	background-size: contain;
	margin-right: 20px;
	float: left;
}
.friendItem:after{
	content: '';
	display: block;
	clear: both;
}
.friendItem span{
	color: rgb(255, 200, 0);
    font-size: 14px;
    letter-spacing: 2px;
    margin-top: 15px;
    display: block;
	font-weight: 400;
}
.friendItem i{
    color: rgb(175, 137, 0);
    font-size: 10px;
	display: inline-block;
	letter-spacing: 1px;
}
.messageItem{
	/* flex-basis: 100%; */
	padding: 10px 0;
	border-bottom:1px solid rgb(129, 101, 0);
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.messageItem span{
	color: rgb(255, 200, 0);
    font-size: 12px;
    letter-spacing: 2px;   
}
.sendMessageForm{
	flex-basis: 100%;
    padding: 10px 20px;
    background-color: rgba(0, 0, 0, 0.6);
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.input{
	width: 70%;
	padding: 20px 20px 0;
	background-color: rgba(0, 0, 0, 0);
	color: white;
	font-family: Intro;
	font-weight: 300;
	text-align: left;
	border-style: none ;
	resize:none;
	outline:none
}

